import Vue from 'vue'
import App from './App.vue'
// import ElementUI from 'element-ui'
// import 'element-ui/lib/theme-default/index.css'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import ListView from './components/ListView.vue'
import Main from './views/Main.vue'
import DetailView from './views/DetailView.vue'
import InfiniteLoading from 'vue-infinite-loading';

Vue.use(VueRouter)
Vue.use(VueResource)

const routes = [
  {path:'/', component : Main , children : [
    {path: '/', component : ListView},
  ]},
  {path:'/detail/:jobId', component : DetailView}
]

var router = new VueRouter({
  routes : routes,
  mode : 'history',
})

new Vue({
  el: '#app',
  render: h => h(App),
  router : router,
  data : {
    temp : [],
    lat : '',
    long : '',
    page : 1,
    isDataOver : false,
    query : '',
    complete: true,
    redirectFromDetail : false,
    currentSearchTerm : '',
    isDataLoadedInitially : false,
    isConnectionBridgeEstablished: false
  },
  methods: {
    getData: function() {
      var self = this;
      // https://walkin.asiatrotter.org/api/v1/nearby?lat=12.9716&lng=77.5946&radius=15&query=&limit=35&page=1&categoryId=1&city=
      self.$http.get('https://walkin.asiatrotter.org/api/v1/nearby?lat='+this.lat+'&lng='+this.long+'&radius=15&query='+this.query+'&limit=10&page='+this.page+'&categoryId=1&city=').then(response => {
        // console.log(response.data[0]);
        // get body data
        if(response.data.length == 0){
          this.isDataOver = true;
          document.querySelector(".infinite-status-prompt").innerHTML = 'No results found';
        }
        else if(response.data.length > 0 && response.data.length < 10) {
          for(var i = 0 ; i < response.data.length ; i ++) {
            response.data[i].distance = Math.round(response.data[i].distance).toFixed(2); 
            self.temp.push(response.data[i])
          }   
          this.isDataOver = true;
          this.isDataLoadedInitially = true;
          document.querySelector(".infinite-status-prompt").innerHTML = 'No more results to show';
        }
        else {
          for(var i = 0 ; i < response.data.length ; i ++) {
            response.data[i].distance = Math.round(response.data[i].distance).toFixed(2); 
            self.temp.push(response.data[i])
          }   
          this.isDataOver = false;
          this.isDataLoadedInitially = true;
        }
      }, response => {
        console.log('error');    // error callback
        this.isDataOver = true;
      });
    },
    getlocation : function () {
      // navigator.geolocation.getCurrentPosition(this.success,this.error);
      var self = this;
      navigator.geolocation.getCurrentPosition(self.success, self.error);
    },
    success : function(position){
      this.lat =  position.coords.latitude;
      this.long = position.coords.longitude;
      this.getData();
      console.log('NOTE : Google Api Working On Secure Origin , Hence Data Is Served')
    },
    error : function(err){
      console.log('NOTE : Google Api Not Working On Secure Origin , Hence Data Not Served')
      console.log(err.code , err.message);
    }
  },
  created : function () {

    this.getlocation();
    
    this.$on('get-next-data',function(){
      this.page++;
      console.log("current page : "+this.page);
      if(this.isDataLoadedInitially==true)
      this.getData();
    });
    this.$on('get-query-data',function(value){
      this.query =  value;
      if(this.query == ''){
      this.page = 1;
      }
      else{
      this.page = ''  
      }
      console.log('On function called');
      console.log('current query : ' + this.query); 
      this.temp = [];
      if(this.isDataLoadedInitially==true)
      this.getData();
    })
  },
  components : {
    InfiniteLoading,
  }
})



