<template>
	<div>
		<div class="detail-bar">
			<router-link to="/">
				<button><img src="../assets/back-icon-light.png"></button>
			</router-link>	
				<p>{{current.title}}</p>
		</div>	
		<a :href='url' class="geo-link"> Get Directions</a>
		<div id="map"></div>	
		<div id="detail-info">
			<div class="detail-info-container">
				<div class="detail-apply">
					<a :href="current.source"><button class="hvr-grow-shadow">Apply</button></a>
				</div>
				<div class="detail-title detail-div">
					<h1 class="left-border">Title : </h1>
				    <p> {{current.title}}</p>
				</div>
				<div class="detail-companyName detail-div">
					<h1 class="left-border">Company Name :  </h1>
					<p> {{current.companyName}}</p>
				</div>
				<div class="detail-description detail-div">
					<h1 class="left-border">Job Description :  </h1> 
					<p> {{current.jobDescription}}</p>
				</div>
				<div class="detail-website detail-div">
					<h1 class="left-border">Website :  </h1>
					<p v-if="current.website != ''"> {{current.website}}</p> 
					<p v-else>N/A</p>
				</div>
				<div class="detail-experience detail-div">
					<h1 class="left-border">Experience :  </h1> 
					<p v-if="current.experience != ''"> {{current.experience}}</p> 
					<p v-else>N/A</p>
				</div>
				<div class="detail-qualification detail-div">
					<h1 class="left-border">Qualification :  </h1> 
					<p> {{current.qualification}}</p>
				</div>
				<div class="detail-salary detail-div">
					<h1 class="left-border">Salary :  </h1> 
					<p v-if="current.phone != ''"> {{current.salary}}</p> 
					<p v-else>N/A</p>
				</div>
				<div class="detail-phone detail-div">
					<h1 class="left-border">Phone : </h1> 
					<p v-if="current.phone != '' ">{{current.phone.substring(0,20).match(/[\d, ]/g).join("")}}</p>
					<p v-else>N/A</p>
				</div> 

				<div class="detail-apply">
					<a :href="current.source"><button class="hvr-grow-shadow">Apply</button></a>
				</div>

			</div>	
		</div>	
	</div>
	
</template>

<script >

	export default {
		data : function() {
			return  {
				param : this.$route.params.jobId,
				maplat : '',
				maplong : '',
				url : ''
			}
		},
		computed : {
			current : function() { 
				return this.$root.$data.temp[this.param];
			},
			geo : function(){
				return 'geo:'+this.maplat+','+this.maplong;
			}
		},
		watch:{
      'current' : function(a,b){
				console.log(a);
				this.getlatlong();
	    },
			'geo' : function(){
				this.url = 'geo:'+this.current.companyName+"?q="+''+this.maplat+','+this.maplong+'('+this.current.companyName+')'
			}
	  },
		methods : {
			getlatlong : function() {
				// console.log(this.current)
				this.$http.get('https://maps.googleapis.com/maps/api/geocode/json?address='+this.current.companyName+','+this.current.workLocation+'&key=AIzaSyCYvnTorY23mtCZNzQLpLxE2GdhuYBKMcM').then(response => {
				this.maplat = response.body.results[0].geometry.location.lat;
				this.maplong = response.body.results[0].geometry.location.lng;    // get body data
				this.initMap();
				}, response => {
				console.log('error');    // error callback
				});
			},
			initMap : function () {
				var mapOptions = {
          // How zoomed in you want the map to start at (always required)
          zoom: 15,

          // The latitude and longitude to center the map (always required)
          center: new google.maps.LatLng(this.maplat, this.maplong), // New York

        };

        // Get the HTML DOM element that will contain your map
        // We are using a div with id="map" seen below in the <body>
        var mapElement = document.getElementById('map');

        // Create the Google Map using our element and options defined above
        var map = new google.maps.Map(mapElement, mapOptions);

        // Let's also add a marker while we're at it
        var marker = new google.maps.Marker({
            position: new google.maps.LatLng(this.maplat, this.maplong),
            map: map,
            title: this.current.companyName,
        });
			} 
		},
		updated : function() {
			this.initMap();
		},
		created : function() {
			this.current = this.$root.$data.temp[this.param];
			this.$root.$data.redirectFromDetail = true;
			this.getlatlong();
		},
	}
	
</script>

<style type="text/css">

	#map{
		width: 100%;
		height: 35vh;
		box-sizing: border-box;
		margin-top: 64px;
	}
	
	#detail-info{
		background-image: url('../assets/preview.jpg');
		width: 100%;
		padding: 10px;
		box-sizing: border-box;
		background-size: cover;
		background-position: center center; 
	}

	.detail-info-container{
		width: 100%;
		height: 100%;
		background-color: white;
		overflow-y: scroll;
		box-sizing: border-box;
		padding: 20px;
		box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	}

	.detail-info-container h1 { 
		font-size: 17px;
		font-weight: bold;
		display: inline-block;
		margin: 0px;
	}
	.detail-info-container p {
		font-weight: lighter;
		display: inline;
		font-size: 15px;
		line-height: 1.35;
		padding-left: 5px;
	}

	.detail-apply {
		text-align: center;
		margin-bottom: 30px;
	}

	.detail-apply button{
		padding: 10px 20px;
		font-size: 15px;
		background-color: #1976D2;
		color: white;
		border-radius: 100px;
		font-weight: bold;
		border : none;
	}
    
    .detail-apply button:focus {outline:0;}

	.hvr-grow-shadow {
	  display: inline-block;
	  vertical-align: middle;
	  -webkit-transform: perspective(1px) translateZ(0);
	  transform: perspective(1px) translateZ(0);
	  box-shadow: 0 0 1px transparent;
	  -webkit-transition-duration: 0.2s;
	  transition-duration: 0.2s;
	  -webkit-transition-property: box-shadow, transform;
	  transition-property: box-shadow, transform;
	}
	.hvr-grow-shadow:hover, .hvr-grow-shadow:focus, .hvr-grow-shadow:active {
	  box-shadow: 0 12px 12px -12px rgba(0, 0, 0, 0.7);
	  -webkit-transform: scale(1.05);
	  transform: scale(1.05);
	}

	.left-border{
		padding-left: 5px;
		border-left: 4px solid #1976D2;
	}

	.detail-div{
		margin-bottom: 30px;
		word-wrap: break-word;
		word-break: break-all;
	}

	.geo-link{
		position: absolute;
	    top: 120px;
	    right: 0px;
	    padding: 10px;
	    font-size: 12px;
	    color: white;
	    background: rgba(25,118,210,0.7);
	    z-index: 2;
	    font-weight: bold;
	    text-decoration: none;
	    display: block;
	}

	.detail-back-button{
		position: absolute;
		top: 60px;
		left: 0px;
		font-weight: bold;
		background: rgba(25,118,210,0.8);
		color : white;
		font-size: 40px;
		padding: 25px 35px;
	}
	.detail-bar{
		background : #1976D2;
		border : none;
		display: inline-block;
		width: 100%;
		text-align: center;
		padding: 20px;
		box-sizing: border-box;
		box-shadow: 0 5px 5px 1px rgba(0,0,0,0.5);
		margin-bottom: 10px;
		position: fixed;
		top: 0;
		z-index: 1000;
	}

	.detail-bar img{
		display: inline-block;
		height: 20px;
		width: 20px;
		padding: 3px;
	}

	.detail-bar p{
		color: white;
		font-size: 15px;
		text-align: center;
		padding: 0px 40px;
		width: 100%;
		margin: 0px;
		box-sizing: border-box;
		font-weight: bold;
		white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
	}

	.detail-bar button {
		position: absolute;
		left: 10px;
		top: 14px;
		background : none;
		border : none;
		float: left;
	}

</style>


