<template>
	<div id="listview">
		<div  v-for="(item,index) in jobsList" class="list" v-on:click="takeroute(index)">
			<div class="list-left">
				<div class="list-favicon-job">
					<i class="fa fa-suitcase"></i>
					<p style="font-weight:bold;">{{item.title}}</p>
				</div>
				<div class="list-favicon-job">
					<i class="fa fa-industry"></i>
					<p>{{item.companyName}}</p>
				</div>
				<div v-if="item.phone != '' " class="list-favicon-job">
					<i class="fa fa-phone"></i>
					<p>{{item.phone.substring(0,20).match(/[\d, ]/g).join("")}}</p>
				</div>
				<div v-else class="list-favicon-job">
					<i class="fa fa-phone"></i>
					<p>N/A</p>
				</div>
			</div>	
			<div class="list-right">	
				<span class="distanceInfo"><i class="fa fa-map-marker"></i><span>{{item.distance}} kms</span></span>
				<a :href="item.source" @click="preventDetail"> <button class="hvr-grow-shadow">Apply</button></a>
			</div>	
		</div>
    <infinite-loading :on-infinite="onInfinite" :distance="0" spinner="spiral" ref="infiniteLoading" >
    	<span slot="no-more">
		  No More results to show
		</span>
		<span slot="no-results">
		  No Results Found
		</span>
    </infinite-loading>
	</div>
</template>

<style type="text/css">

#listview{
	padding-top: 60px;
	font-family: 'Roboto Condensed', sans-serif;
	height: 100vh;
	overflow-y: scroll;
	overflow-x: hidden;
	font-size-adjust: 0.19;
}
	
.list{
	background: white;
	padding: 10px;
	margin: 10px;
	display: flex;
	flex-direction: row;
	box-shadow: 0 5px 10px rgba(0,0,0,0.19), 0 3px 3px rgba(0,0,0,0.23);
}

.list-left{
	width: 80%;
}

.list-right{
	width: 20%;
	position: relative;
	text-align: center;
}

.distanceInfo{
	position: absolute;
    top: 5px;
    right: 5px;
}

.list-right i {
	font-size: 12px;
	margin-right: 5px;
}

.list-right span{
	font-size: 10px;
}

.list-right button {
	position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto auto;
    font-size: 12px;
    border: 0px;
    height: 30px;
    width: 18vw;
    padding: 0px 5px;
    color: white;
    letter-spacing: 1px;
    background-color: #1976D2;
    border-radius: 50px;
}

.list-right button:focus {outline:0;}

.list-favicon-job{
	display: flex;
	flex-direction: row;
	color : #babcbe;
}

.list-favicon-job p {
	margin: 0px;
	flex-grow: 1;
	padding: 5px;
	font-size: 13px;
	color: darkslategrey;
}

.list-favicon-job i{
	padding: 5px;
	width: 5%;
	font-size: 13px;
} 

.infinite-status-prompt{
	font-size: 15px !important;
	color: white !important;
}

.infinite-loading-container [class^=loading-][data-v-50793f02] {
    display: inline-block;
    margin: 15px 0;
    width: 25px !important;
    height: 25px !important;
    font-size: 18px;
    line-height: 28px;
    border-radius: 50%;
}

.loading-default{
	border : 1px solid white !important;
}

.infinite-loading-container{
	padding: 5px !important;
}

</style>

<script type="text/javascript">
	import InfiniteLoading from 'vue-infinite-loading';
	export default {
		computed : {
			jobsList : function(){
				return this.$root.$data.temp;
			},
			isDataOver : function(){
				return this.$root.$data.isDataOver;
			}
		},
		methods : {
			takeroute : function(index) {
				this.$router.push('/detail/'+index+'');
				console.log(index);
			},
			preventDetail : function(event) {
				event.stopPropagation();
			},
			onInfinite : function() {
				setTimeout(() => {
			        
			        if(!this.$root.$data.isDataOver){
			         this.$root.$emit('get-next-data');
			         this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
			        }
			     	else{
			     	 console.log("Data is over");	
			     	 this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');	
			     	}
		        }, 1500);
			}  
		},
		components : {
			InfiniteLoading
		},
		updated : function(){
			if(this.isDataOver == false){
			        	this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');	
			}
			console.log('updated');
		},
	}
</script>


